<template>
  <div class="input-field">
    <div class="input-icon">
      <i class="iconfont" :class="icon"></i>
    </div>
    <label for="" class="input-label">{{ label }}</label>
    <input
      type="text"
      :value="value"
      @input="inputHandler($event)"
    >
  </div>
</template>

<script>
export default {
  props: {
    icon: {
      type: String,
      default: 'icon-username'
    },
    label: {
      type: String,
      default: '请输入....'
    },
    autofocus: {
      type: Boolean,
      default: false
    },
    value: {
      type: [String, Number],
      default: ''
    },
    type: {
      type: String,
      default: 'text'
    }
  },
  methods: {
    inputHandler (ev) {
      let target = ev.target
      // 清除前后空格
      let value = target.value.trim()
      // 如果是number类型表单，做过滤处理
      if (this.type === 'number') {
        target.value = target.value.replace(/[^\d]/g, '')
        value = Number(target.value)
      }
      this.$emit('input', value)
    }
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  .input-field
    color: $text-color
    text-align: center
    margin-bottom: 20px
    .input-icon
      margin-bottom: 20px
      .iconfont
        font-size: $font-size-large-xxl
    .input-label
      display: block
      font-size: $font-size-large-xl
    input
      width: 300px
      padding: 5px 10px
      font-size: $font-size-large-xxl
      outline: none
      text-align: center
      border: none
      border-bottom: 1px solid $text-color
      letter-spacing: 2px
    @media screen and (max-width: 480px)
      input
        width: 240px
        padding: 5px 1px
</style>




