<template>
  <form @submit.prevent="submitHandler">
    <input-field
      label="请输入您的名字"
      icon="icon-username"
      v-model="username"
      :autofocus="true"
      class="username"
    />
    <input-field
      label="请输入您的手机号码"
      icon="icon-phone"
      v-model="phone"
      type="number"
      class="phone"
    />
    <input type="submit" value="确定"/>
  </form>
</template>

<script>
import InputField from 'base/inputField/inputField'
import { searchFriend } from 'api/search'

export default {
  data () {
    return {
      username: '',
      phone: ''
    }
  },
  methods: {
    // 提交表单
    submitHandler () {
      const { username, phone } = this
      searchFriend({
        username,
        phone
      }).then(res => {
        console.log(res)
        // 先弹出了，之后再封装弹窗组件
        alert(JSON.stringify(res))
      }).catch(err => {
        if (err) {
          alert('网络出错!')
        }
      })
    }
  },
  components: {
    InputField
  }
}
</script>

<style lang="stylus" scoped>
  @import '~common/stylus/theme'

  form
    position: absolute
    top: 50%
    left: 50%
    transform: translate(-50%, -50%)
    width: 500px
    text-align: center
    input[type=submit]
      width: 300px
      text-align: center
      line-height: 50px
      background-color: $text-color
      color: #fff
  @media screen and (max-width: 480px)
    form
      width: 240px
      input[type=submit]
        width: 230px
</style>



